.o-list {
	font-size: 16px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.5;
	box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
	transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
	will-change: box-shadow;
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.o-list-item {
	display: flex;
	flex-direction: row;
	position: relative;
	overflow: hidden;
	justify-content: flex-start;
	align-items: center;
	padding: 16px;
}

.o-list-media {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	flex-shrink: 0;
	margin-right: 16px;
	margin-left: 0;
}

.o-list-media-icon {
	width: 40px;
	height: 40px;
}

.o-list-primary, .o-list-secondary, .o-list-meta-primary, .o-list-meta-secondary {
	display: block;
}

.o-list-primary {
	line-height: 1.25;
}

.o-list-secondary {
	font-size: 14px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.25;
	text-decoration: inherit;
	text-transform: inherit;
}

.o-list-secondary-item {
	line-height: 1.2857142857;
	padding-right: 16px;
}

.o-list-secondary-item-icon {
	width: 18px;
	height: 18px;
	margin-right: 8px;
}

.o-list-secondary-item-link:hover {
	text-decoration: underline;
}

.o-list-meta {
	margin-right: 0;
	margin-left: auto;
}

.o-list-meta-link:hover {
	text-decoration: underline;
}

.o-list-meta-primary {
	line-height: 1.25;
}

.o-list-meta-secondary {
	font-size: 14px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.25;
	text-decoration: inherit;
	text-transform: inherit;
}

.o-list-meta-media {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	flex-shrink: 0;
	margin-right: 0;
	margin-left: 8px;
}

.o-list-avatar {
	width: 40px;
	height: 40px;
	margin: 0;
	padding: 0;
}

@keyframes grow {
	0% {
		opacity: 0;
		transform: scaleX(0.1);
	}
	50% {
		transform: scaleX(0.99);
	}
	100% {
		opacity: 1;
		transform: scaleX(1);
	}
}

@keyframes shrink {
	0% {
		opacity: 1;
		transform: scaleX(1);
	}
	50% {
		transform: scaleX(0.01);
	}
	100% {
		opacity: 0;
		transform: scaleX(0);
	}
}

