.o-bar-action {
	font-size: 16px;
	letter-spacing: 0.04em;
	font-weight: 700;
	line-height: 1.5;
	color: #757575;
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 56px;
	overflow: hidden;
	padding: 16px;
	transform: translate3d(0, 0, 0);
}

.o-bar-action:after {
	background-image: radial-gradient(circle, #000000 10%, transparent 10.01%);
	background-repeat: no-repeat;
	background-position: 50%;
	opacity: 0;
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	content: "";
	transition: transform 0.5s, opacity 1s;
	transform: scale(10, 10);
	pointer-events: none;
}

.o-bar-action:active:after {
	opacity: 0.2;
	transition: 0s;
	transform: scale(0, 0);
}

.o-bar-action:hover, .o-bar-action:focus {
	background-color: #eeeeee;
	outline: 0;
	color: #258cf4;
	transition: 0.5s;
}

